<script>
import backgroundImage from "@/assets/DailyOperation/background/1-2.jpg";
import LineChartLimit from "@/components/LineChartLimit";
import websockObject from "@/mixins/websock";
const tickcount = 20;
export default {
  name: "CapacityLineChart",
  mixins: [websockObject],
  data() {
    return {
      fristBack: true,
      // eslint-disable-next-line no-undef
      orderObject: dailyLeftCenter,
      backgroundImage,
      CapacityLineHeader: [],
      CapacityLineLeft: [],
      CapacityLineRight: []
    };
  },
  components: {
    LineChartLimit
  },
  mounted() {
    // eslint-disable-next-line no-undef
    this.wsuri = BaseUrl.dailyLeftCenter.websockUrl;
    // this.useTheTestDate()
  },
  methods: {
    useTheTestDate() {
      let tillTiem = new Date().getTime();
      let m = 10000;
      let n = 0;
      let test = [
        {
          dateTime: tillTiem,
          limit: 0,
          type: "top",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "中间件ar",
          group: "中间件ar"
        },
        {
          dateTime: tillTiem,
          limit: 0,
          type: "top",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "as",
          group: "as"
        },
        // {'dateTime': tillTiem, limit: 0, type: 'top', 'value': Math.floor(Math.random() * (m - n) + n), 'indicateName': '算法服务', 'group': '算法服务'},
        {
          dateTime: tillTiem,
          limit: 0,
          type: "top",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "数据库",
          group: "数据库"
        },

        // {'dateTime': tillTiem, limit: 0, type: 'left', 'value': Math.floor(Math.random() * (m - n) + n), 'indicateName': '前置机1', 'group': '前置机'},
        // {'dateTime': tillTiem, limit: 0, type: 'left', 'value': Math.floor(Math.random() * (m - n) + n), 'indicateName': '前置机2', 'group': '前置机'},
        // {'dateTime': tillTiem, limit: 0, type: 'left', 'value': Math.floor(Math.random() * (m - n) + n), 'indicateName': '前置机3', 'group': '前置机'},
        // {'dateTime': tillTiem, limit: 0, type: 'left', 'value': Math.floor(Math.random() * (m - n) + n), 'indicateName': '前置机4', 'group': '前置机'},

        {
          dateTime: tillTiem,
          limit: 0,
          type: "left",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "算法服务器1",
          group: "算法服务器"
        },
        {
          dateTime: tillTiem,
          limit: 0,
          type: "left",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "算法服务器2",
          group: "算法服务器"
        },
        {
          dateTime: tillTiem,
          limit: 0,
          type: "left",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "数据库1",
          group: "数据库"
        },
        {
          dateTime: tillTiem,
          limit: 0,
          type: "left",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "数据库2",
          group: "数据库"
        },

        {
          dateTime: tillTiem,
          limit: 0,
          type: "right",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "数据库",
          group: "数据库"
        },
        {
          dateTime: tillTiem,
          limit: 0,
          type: "right",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "分仓报盘1",
          group: "分仓报盘"
        },
        {
          dateTime: tillTiem,
          limit: 0,
          type: "right",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "分仓报盘2",
          group: "分仓报盘"
        },
        {
          dateTime: tillTiem,
          limit: 0,
          type: "right",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "行情交易1",
          group: "行情交易"
        },
        {
          dateTime: tillTiem,
          limit: 0,
          type: "right",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "行情交易2",
          group: "行情交易"
        }
      ];
      this.getWebsocketMsg(test);
      let _this = this;
      this.timeOut && clearTimeout(this.timeOut);
      this.timeOut = setTimeout(() => {
        _this.useTheTestDate();
      }, 2000);
    },
    getWebsocketMsg(sourDate) {
      let self = this;
      if (sourDate.length > 0) {
        let historyDate = [];
        sourDate.forEach(element => {
          element.group = element.group
            .replace(/\s+/g, "")
            .replace("迅投pb_", "")
            .replace("络町pb_", "")
            .replace("恒生pb_", "")
            .toUpperCase();
          if (element.type === "top") {
            historyDate = self.CapacityLineHeader;
          }
          if (element.type === "left") {
            historyDate = self.CapacityLineLeft;
          }
          if (element.type === "right") {
            historyDate = self.CapacityLineRight;
          }
          let target = historyDate.findIndex(item => {
            return item.group === element.group;
          });
          if (target === -1) {
            let item = {
              indicateList: [
                {
                  indicateName: element.indicateName,
                  value: []
                }
              ],
              group: element.group
            };
            item.indicateList[0].value.push(element);
            if (element.type === "top") {
              self.CapacityLineHeader.push(item);
            }
            if (element.type === "left") {
              self.CapacityLineLeft.push(item);
            }
            if (element.type === "right") {
              self.CapacityLineRight.push(item);
            }
          } else {
            let item = historyDate[target];
            let isIndicateNameSet = item.indicateList.findIndex(
              indicateitem => {
                return indicateitem.indicateName === element.indicateName;
              }
            );
            if (isIndicateNameSet === -1) {
              let indicate = {
                indicateName: element.indicateName,
                value: []
              };
              indicate.value.push(element);
              item.indicateList.push(indicate);
              item.indicateList.sort((a, b) => {
                return (
                  Number(a.indicateName.replace(/[^\d]/g, " ")) -
                  Number(b.indicateName.replace(/[^\d]/g, " "))
                );
              });
            } else {
              let base = item.indicateList[isIndicateNameSet].value;
              base.push(element);
              item.indicateList[isIndicateNameSet].value = base.slice(
                -1 * tickcount
              );
            }
            if (element.type === "top") {
              self.$set(self.CapacityLineHeader, target, item);
            }
            if (element.type === "left") {
              self.$set(self.CapacityLineLeft, target, item);
            }
            if (element.type === "right") {
              self.$set(self.CapacityLineRight, target, item);
            }
          }
        });
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
    }
  },
  render(h) {
    return (
      <section
        class="mainBox"
        {...{
          style: { backgroundImage: " url(" + this.backgroundImage + ")" }
        }}
      >
        <section class="mainChartBox mainChartHeader">
          <div class="title mainTitile" {...{ style: { top: "-15px" } }}>
            {this.orderObject[0].title}
          </div>
          <section class="LineChartBox">
            <section
              class="contentBox"
              {...{ style: { width: "1786px", height: "280px" } }}
            >
              {this.CapacityLineHeader.map((item, index) => {
                return (
                  <div
                    class="LineChartItem"
                    key={
                      "CapacityLineHeader" + index + item.indicateList.length
                    }
                    {...{
                      style: {
                        order: this.orderObject[0].order.indexOf(item.group),
                        width: "427px"
                      }
                    }}
                  >
                    {item.indicateList.map(indicateItem => {
                      return (
                        <LineChartLimit
                          sourDate={indicateItem.value}
                          title={indicateItem.indicateName}
                        ></LineChartLimit>
                      );
                    })}
                  </div>
                );
              })}
            </section>
          </section>
        </section>
        <section class="mainChartBox mainChartLeft">
          <div class="title mainTitile">{this.orderObject[1].title}</div>
          <section class="LineChartBox">
            <section
              class="contentBox"
              {...{
                style: {
                  width: "880px",
                  height: "600px",
                  position: "absolute",
                  right: "0",
                  top: "20px"
                }
              }}
            >
              {this.CapacityLineLeft.map((item, index) => {
                return (
                  <div
                    class="LineChartItem"
                    key={"CapacityLineLeft" + index + item.indicateList.length}
                    {...{
                      style: {
                        order: this.orderObject[1].order.indexOf(item.group),
                        width: "273px"
                      }
                    }}
                  >
                    {item.indicateList.map(indicateItem => {
                      return (
                        <LineChartLimit
                          sourDate={indicateItem.value}
                          title={indicateItem.indicateName}
                        ></LineChartLimit>
                      );
                    })}
                  </div>
                );
              })}
            </section>
          </section>
        </section>
        <section class="mainChartBox mainChartRight">
          <div class="title mainTitile">{this.orderObject[2].title}</div>
          <section class="LineChartBox">
            <section
              class="contentBox"
              {...{
                style: {
                  width: "880px",
                  height: "600px",
                  position: "absolute",
                  right: "0",
                  top: "20px"
                }
              }}
            >
              {this.CapacityLineRight.map((item, index) => {
                return (
                  <div
                    class="LineChartItem"
                    key={"CapacityLineRight" + index + item.indicateList.length}
                    {...{
                      style: {
                        order: this.orderObject[2].order.indexOf(item.group),
                        width: "273px"
                      }
                    }}
                  >
                    {item.indicateList.map(indicateItem => {
                      return (
                        <LineChartLimit
                          sourDate={indicateItem.value}
                          title={indicateItem.indicateName}
                        ></LineChartLimit>
                      );
                    })}
                  </div>
                );
              })}
            </section>
          </section>
        </section>
      </section>
    );
  }
};
</script>

<style scoped>
.mainChartBox {
  display: flex;
  flex-flow: column;
}
.mainChartHeader {
  height: 380px;
  width: 1833px;
  position: absolute;
  top: 0;
  left: 60px;
}
.mainChartLeft {
  width: 911px;
  height: 700px;
  position: absolute;
  bottom: 0;
  left: 60px;
}
.mainChartRight {
  width: 911px;
  height: 700px;
  position: absolute;
  bottom: 0;
  right: 60px;
}
.title {
  position: relative;
  width: auto;
  font-size: 30px;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 2px;
  line-height: 1;
  text-align: left;
  left: 0;
  top: -5px;
  padding: 25px;
}
.LineChartBox {
  width: 100%;
  flex: auto;
  position: relative;
}
.contentBox {
  width: 100%;
  margin: auto;
  height: 100%;
  display: flex;
}
.LineChartItem {
  width: 33.3%;
  height: 100%;
  display: flex;
  flex-flow: column;
  padding: 0 10px;
}
.LineChartName {
  text-align: left;
  width: 100%;
  margin: auto;
  margin-left: 2%;
  line-height: 1;
}
.LineChartLimit {
  height: 100%;
}
</style>



